.shop__cta button {
  width: 100%;
  background: transparent;
  border: 1px solid #f51167;
  color: #4c4c4c;
  transition: all 200ms ease-in;
}
.shop__cta button:hover {
  background: #f51167;
  color: #fff;
}
.category__title {
  text-align: center;
  color: #4c4c4c;
}
.filter__sort,
.dress__categories {
  display: none;
}
.filter__sort.active,
.dress__categories.active {
  position: fixed;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  top: 0;
  left: 0;
  background: #eeeeee;
  z-index: 80;
}
.filter__sort h2,
.dress__categories h2 {
  color: #f51167;
  font-size: 50px;
}
.filter__sort .filter_demarcator,
.dress__categories .filter_demarcator {
  border: 1px solid #f51167;
  margin-top: 10px;
  margin-bottom: 30px;
  width: 100px;
}
.categories__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 20px;
}
.categories__container .btn {
  border: 1px solid #f51167;
  color: #4c4c4c;
  margin: 10px 10px;
  transition: all 200ms ease-in;
}
.categories__container .btn:hover {
  color: #fff;
  background: #f51167;
}
.demarcator {
  width: 75%;
}
.filter__sort h3 {
  color: #4c4c4c;
}
.filter__sort .btn {
  padding: 10px 0;
  width: 75%;
  color: #fff;
  display: flex;
  margin: 0 auto;
  justify-content: center;
  background: #f51167;
}
#filter_checkboxes input {
  display: none;
}
#filter_checkboxes label {
  border: 1px solid #f51167;
  padding: 10px 15px;
  border-radius: 5px;
  color: #4c4c4c;
  cursor: pointer;
}
#filter_checkboxes input:checked + label {
  color: #fff;
  background: #f51167;
}

/* DESKTOP */

@media only screen and (min-width: 1024px) {
  .filter__sort.active .categories__container {
    width: 80%;
    margin: 0 auto;
  }
}

/* MOBILE */

@media only screen and (max-width: 480px) {
  .filter__sort.active {
    height: 92vh;
    margin-top: 8vh;
  }
  .filter__sort #filter_checkboxes {
    margin-top: 0 !important;
  }
  .filter__sort hr.demarcator {
    margin-top: 0;
  }
  #filter_checkboxes label {
    padding: 5px 8px;
  }
  .dress__categories h2 {
    font-size: 40px;
  }
  .filter__sort.active h2 {
    font-size: 25px;
    margin-bottom: 30px;
  }
  .filter_sort hr,
  .dress__categories hr {
    margin-bottom: 10px !important;
  }
  .filter__sort h3 {
    display: none;
  }
}
@media only screen and (max-width: 375px) {
  .filter_sort {
    padding: 18px 0;
  }
  .dress__categories h2 {
    font-size: 30px;
    margin-top: 50px;
  }
}
